<template>
<div>
  <h1>Items on Credit</h1>
  <div v-for="offer in offers">
    <b-card>
      <b-row>
        <b-col lg="3">

        </b-col>
        <b-col lg="6">
          <h3>{{offer.Title}}</h3>
          <p>{{offer.Description}}</p>
        </b-col>
        <b-col lg="3">

          <h4>Requirements</h4>
          <b-row align-v="end" align-h="end">
            <b-btn @click="apply(offer)" variant="success">Apply</b-btn>
          </b-row>

        </b-col>
      </b-row>
    </b-card>

  </div>
</div>
</template>

<script>
  import { mapGetters } from 'vuex'
  export default {
    name: '',
    computed: {...mapGetters({offers: '$_dashboard/offers'})},
    methods: {
      apply (offer) {
        console.log(offer)
        this.$store.dispatch('$_dashboard/APPLY_CREDIT', offer.Id)
      }
    }
  }
</script>

<style scoped>

</style>



